$white:#fff;
$black:#000;
$black-lite:#020000;
$green:#3d8c03;
$orange:#f8bb32;
$grey:#616161;
$grey06:rgba(#616161, 0.6);
$grey-light:#898888;
$grey-light05:rgba(#898888, 0.5);
$border:#c2c2c2;


@import "fonts";
@import "libs/bootstrap/bootstrap";
@import "libs/bootstrap/bootstrap-reboot";
@import "libs/bootstrap/bootstrap-grid";

@import "libs/slick/slick";
@import "libs/slick/slick-theme";
@import "sliders";
@import "libs/font-awesome/font-awesome";

@import "libs/fansybox/jquery.fancybox";
@import "checkbox-radio";

@import "page-zakaz";

//standart
body{
  color:$black;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
}

*{
  outline: none;
  &:focus, &:hover, &:active{
    outline: none;
  }
}
.btn.focus, .btn:focus{
  box-shadow: none;
}
#home-land{
  overflow: hidden;
}
input, textarea{
  outline: none;
  border:none;
}
::-webkit-input-placeholder {font-size: 16px;color:$grey-light05;}
::-moz-placeholder          {font-size: 16px;color:$grey-light05;}/* Firefox 19+ */
:-moz-placeholder           {font-size: 16px;color:$grey-light05;}/* Firefox 18- */
:-ms-input-placeholder      {font-size: 16px;color:$grey-light05;}
label{
  margin-bottom: 0;
}
ul{
  padding: 0;
  margin: 0;
  li{
    list-style: none;
  }
}
a{
  &:hover, &:focus{
    outline: none;
  }
}
img{
  max-width: 100%;
}
.clear{
  clear:both;
}
.bg-gray{
  background: #f6f4f5;
}
.btn{
  display: inline-block;
  width: 270px;
  background: $orange;
  color:$white;
  text-transform: uppercase;
  font-size: 14px;
  height: 50px;
  line-height: 51px;
  text-align: center;
  transition: all 0.5s ease;
  &:hover{
    transition: all 0.5s ease;
    background-color: rgba(242, 128, 88, .7);
    color:$white;
  }
}


.h1{
  font-size: 138px;
  line-height: 1.2;
  letter-spacing: 4px;
  color:$white;
  margin-top: -7px;
}
.h2{
  font-size: 60px;
  line-height: 1.2;
  letter-spacing: 28px;
  color:$black;
  text-transform: uppercase;
}
.h3{
  font-size: 48px;
  line-height: 1.2;
  margin-bottom: 12px;
}
.h4{
  font-size: 10px;
  line-height: 1.2;
  letter-spacing: 4px;
  color: $orange;
  text-transform: uppercase;
  display: inline-block;
  position: relative;
  padding-left: 102px;
  margin-bottom: 25px;
  &:before{
    content: "";
    position: absolute;
    box-sizing: border-box;
    width: 80px;
    height: 1px;
    background: $orange;
    left: 0;
    top:4px;
  }
}
.h5{
  font-size: 14px;
  line-height: 1.2;
  color:$black;
}
.h6{
  font-size: 12px;
  line-height: 1.2;
  letter-spacing: 2px;
  color:$white;
  text-transform: uppercase;
  display: inline-block;
  position: relative;
  padding-left: 234px;
  padding-right: 234px;
  &:before{
    content:"";
    position: absolute;
    width: 204px;
    height: 1px;
    background: $white;
    left: 0;
    top:6px;
  }
  &:after{
    content:"";
    position: absolute;
    width: 204px;
    height: 1px;
    background: $white;
    right: 0;
    top:6px;
  }

}
.text{
  font-size: 14px;
  line-height: 28px;
}


.hr{
  height: 1px;
  background: #e5e3e4;
}






