.checkbox__title{
  font-size: 18px;
  color:$grey-light;
  margin-bottom: 20px;
}

.checkbox input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}
.checkbox__text {
  position: relative;
  padding: 0 0 0 30px;
  cursor: pointer;
}
.checkbox__text:before {
  content: '';
  position: absolute;
  top: 3px;
  left: 0;
  width: 19px;
  height: 19px;
  border:1px solid #eee;
  border-radius: 3px;
  background: #3d8c03;
  //box-shadow: inset 0 2px 3px rgba(0,0,0,.2);
  transition: .2s;
}
.checkbox__text:after {

  content: '';
  position: absolute;

  opacity: 0;
  top: 7px;
  left: 5px;
  width: 19px;
  height: 19px;
  background: url("../images/gal.png") no-repeat;
  //box-shadow: 0 2px 5px rgba(0,0,0,.3);
  transition: .2s;
}
.checkbox input:checked + .checkbox__text:before {
  //background: #9FD468;
}
.checkbox input:checked + .checkbox__text:after {
  opacity: 1;
  //left: 26px;
}
.checkbox input:focus + .checkbox__text:before {
  //box-shadow: inset 0 2px 3px rgba(0,0,0,.2), 0 0 0 3px rgba(255,255,0,.7);
}



.radio input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 7px;
}
.radio__text {
  position: relative;
  padding: 0 0 0 35px;
  cursor: pointer;
  font-size: 18px;
  line-height: 21px;
  color:$grey-light05;
  @media (max-width:767px) {
    margin-bottom: 15px;
  }
}
.radio__text:before {
  content: '';
  position: absolute;
  top: 3px;
  left: 0;
  width: 20px;
  height: 20px;
  border: 2px solid #a1a1a1;
  border-radius: 50%;
  background: #FFF;
}
.radio__text:after {
  content: '';
  position: absolute;
  top: 8px;
  left: 5px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #3d8c03;
  //box-shadow: inset 0 1px 1px rgba(0,0,0,.5);
  opacity: 0;
  transition: .2s;
}
.radio input:checked + .radio__text:after {
  opacity: 1;
}
.radio input:checked + .radio__text:before {
  border: 2px solid #3d8c03;
}
.radio input:focus + .radio__text:before {
  border: 2px solid #3d8c03;
}
