.page-zakaz{
  background: url("../images/bg-pageZakaz.png") no-repeat center;
  background-size: cover;
  min-height: 100vh;
}
.header-zakaz{
  background: rgba($green, 0.5);
  padding: 20px 0;
  margin-bottom: 50px;

}
.header-zakaz-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.header-zakaz__callback{
  text-align: right;
}

.zakaz-block-form{
  background: $white;
  padding: 24px 30px 0;
  margin-bottom: 20px;
  border-radius: 5px;
  border:1px solid #c2c2c2;
  &__h2{
    font-size: 24px;
    padding-bottom: 25px;
    margin-bottom: 20px;
    font-weight: normal;
    line-height: 1.2;
    color:$grey;
    text-transform: uppercase;
    position: relative;
    &:before{
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background: #81827d;
      bottom: 0;
    }
  }
  @media (min-width:991px) and (max-width: 1199px) {
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
  }
}


.app-form__fieldset{
  width: 100%;
  margin-bottom: 20px;
}
.app-form__fieldset-title{
  font-size: 16px;
  color:$grey-light;
  span{
    font-size: 24px;
    color:$orange;
  }
}
.app-form__input{
  width: 100%;
  height: 50px;
  padding: 0 20px;
  border-radius: 5px;
  border:1px solid #efefef;
  color:$grey-light05;
}
.app-form__submit{
}
.zakaz__title{
  font-size: 16px;
  color:$grey-light;
}
.zakaz__name{
  font-size: 20px;
  color:$grey06;
}
.zakaz__price{
  font-size: 16px;
  color:$grey-light;
  padding-bottom: 30px;
  span{
    font-size: 20px;
    font-weight: bold;
  }
}
.zakaz-detail{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .zakaz-detail__quantity{
    max-width: 120px;
    .zakaz__title{
      text-align: center;
    }
  }
}
.zakaz-bottom{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background: $green;
  margin-left: -30px;
  margin-right: -30px;
  padding: 30px 30px;
  position: relative;

  .zakaz-foto{
    position: relative;

    img{
      border-radius: 5px;
    }
  }
}
.zakaz-result{
  width: 68%;
  .zakaz-result__title{
    font-size: 22px;
    color:$black-lite;
  }
  .zakaz-result__summa{
    font-size: 30px;
    color:$black;
    margin-bottom: 23px;
    .summa{
      font-size: 46px;
    }
  }

  .app-form__fieldset-submitZakaz{
    width: 100%;
    max-width: 186px;
    .app-form__submit{
      width: 100%;
      color:$grey;

      letter-spacing: 1px;
      border-radius: 5px;
      font-weight: 500;
      &:hover{
        background: $white;

      }
    }
  }
}

.quantity-counter{
  max-width: 120px;
  padding: 0 20px 0 30px;
  position: relative;
  .counter{
    max-width: 100%;
    height: 30px;
    border: 1px solid #C7C7C7;
    background-color: #ECECEC;
    color: #777777;
    font-size: 15px;
    font-weight: 700;
    text-align: center;
    display: block;
    width: 100%;
  }
  .ctrl{
    width: 14px;
    height: 17px;
    overflow: hidden;
    text-indent: 50px;
    position: absolute;
    cursor: pointer;
    background-color: red;
    opacity: .6;
    z-index: 1;
    top: 50%;
    margin: -8px 0 0;
    transition: all 300ms ease;
    &.minus{
      left: 10px;
      background: url("../images/ctrlminus.png") no-repeat;
    }
    &.plus{
      right: 0px;
      background: url("../images/ctrlplus.png") no-repeat;
    }
  }

}

.dostavka{
  border-bottom:  2px dashed #eee;
  padding:30px 0;
  .dostavka-payment{
    display: flex;
    flex-wrap: wrap;
    padding: 0 0 0 63px;
    margin-bottom: 7px;
    .dostavka-payment__radio{
      width: 50%;
    }

  }

  .dostavka-adres{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 0 0 63px;
    .app-form__fieldset{
      width: 50%;
      margin-bottom: 10px;
      .app-form__fieldset-title{
        font-size: 14px;
        span{
          font-size: 14px;
        }
      }
      .app-form__input{
        background: #f7f7f7;
        max-width: 225px;
        height: 42px;
        color:$grey;
      }
      &.dostavka-adres__ukrposhta{
        width: 100%;
      }
    }
  }
  @media (max-width:767px) {
    .dostavka-payment{
      padding: 0 0 0 30px;
      .dostavka-payment__radio{
        width: 100%;
      }
    }
    .dostavka-adres{
      padding: 0 0 0 30px;
      .app-form__fieldset{
        width: 100%;
        .app-form__input{
          max-width: 100%;
        }
      }
    }
  }
}

.payment__title{
  font-size: 16px;
  color:$grey-light;
  margin-bottom: 10px;
  padding: 0 0 0 30px;
}

//thank-zakaz
.thank-zakaz{
  background: $white;
  border-radius: 50px;
  text-align: center;
  padding: 70px 112px;
  max-width: 650px;
  margin-bottom: 100px;
  box-shadow: 0.5px 0.866px 21px 0px rgba( 0, 0, 0,0.5);
  &__title{
    font-size: 22px;
    color:#5c5b5b;
    text-transform: uppercase;
    margin-bottom: 17px;
  }
  &__text{
    font-size: 22px;
    color:#898888;
    margin-bottom: 10px;
  }
  &__itogo{
    font-size: 18px;
    color:#898888;
    margin-bottom: 17px;
    .summa{
      font-size: 30px;
      color:$orange;
      font-weight: 700;
    }
  }
  &__links{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .thank-zakaz__link{
      height: 42px;
      line-height: 42px;
      text-align: center;
      font-size: 16px;
      color:rgba(0,0,0,0.8);
      text-transform: uppercase;
      background: $orange;
      border-radius: 3px;
      width: 183px;
      position: relative;
      span{
        display: block;
        font-size: 12px;
        line-height: 0;
        text-transform: none;
        position: absolute;
        bottom: 8px;
        left: 0;
        right: 0;
      }
      &:hover{
        text-decoration: none;
      }
    }
  }
  @media (max-width:435px) {
    .thank-zakaz__links{
      .thank-zakaz__link{
        width: 100%;
        margin-bottom: 15px;
      }
    }
  }
  @media (max-width:767px) {
    padding: 30px;
  }
}



