.slick-slider{
  padding-bottom: 30px;
  &.slick-dotted{
    margin-bottom: 41px;
  }
  .slick-dots {
    bottom: 0px;
    li {
      height: 16px;
      width: 16px;
      button {
        height: 16px;
        width: 16px;
        &:before {
          background: $white;
          border:1px solid $black;
          width: 16px;
          height: 16px;
          border-radius: 50%;
          opacity: 1;
        }

      }
      &.slick-active button:after{
        content: "";
        position: absolute;
        top:0;
        left:0;
        right:0;
        bottom:0;
        text-align: center;
        margin: auto;
        width: 6px;
        height: 6px;
        background: $black;
        border-radius: 50%;
      }
    }
  }
  .slick-arrow{
    &:hover{
      cursor: pointer;
    }
  }
  .prevArrow{
    width: 40px;
    height: 21px;
    background: url("../images/arrowPrev.png") no-repeat;
    z-index: 1;
    position: absolute;
    top:46%;
    left: 20px;
  }
  .nextArrow{
    width: 40px;
    height: 21px;
    background: url("../images/arrowNext.png") no-repeat;
    z-index: 1;
    position: absolute;
    top:46%;
    right: 20px;
  }
}






